import React, { useState, useEffect } from "react";
import { Layout, Menu, Image } from "antd";
import { LaptopOutlined } from "@ant-design/icons";
import Logo from "../assets/icon.png";
import { Link } from "react-router-dom";
import axios from "axios";
const { SubMenu } = Menu;

export const Sider = () => {
  const [categories, setCategories] = useState(null);
  useEffect(() => {
    getData();
  }, []);
  const getData = async () => {
    const result = await axios.get(
      "https://api.github.com/repos/quankori/kori-book/git/trees/main"
    );
    const dataCategories = result.data?.tree || [];
    let array = [];
    for (let index = 0; index < dataCategories.length; index++) {
      const category = dataCategories[index];
      if (category.type === "tree" && category.path !== "images") {
        const resultCategorySub = await axios.get(category.url);
        array.push({
          title: category.path,
          blogs: resultCategorySub.data?.tree || [],
        });
      }
    }
    setCategories(array);
  };
  return (
    <Layout.Sider breakpoint="md" collapsedWidth="0" className="layout-sider">
      <div
        className="logo"
        style={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Image width={50} src={Logo} />
      </div>
      <Menu theme="dark" mode="inline">
        {categories &&
          categories.map((x, key) => {
            return (
              <SubMenu icon={<LaptopOutlined />} title={x.title} key={key}>
                {x.blogs.map((blog) => {
                  console.log(x.title);
                  console.log(blog);
                  const url = `/blog?folder=${x.title}&path=${blog.path}`;
                  return (
                    <Menu.Item key={blog.path}>
                      <Link to={url}>
                        {blog.path.replace(".md", "")}
                      </Link>
                    </Menu.Item>
                  );
                })}
              </SubMenu>
            );
          })}
      </Menu>
    </Layout.Sider>
  );
};
